import { format } from '@/common/js/format.js'
import getDetail from '@/components/getdetail/getdetail.vue'
export default {
	name: "home-recommend",
	data(){
		return {
			recommend: [],
			months: [],
			hots: [],
			params: {
				limit: 30,
				order: 'hot',
				skip: 0
			},
			// 监听是否还有图片
			isEnd: false,
			// 监听是否发生滚动并触发返回顶部按钮
			backTopValue:false,
			scrollTop: 0,
			old: {
			    scrollTop: 0
			}
		}
	},
	methods: {
		getData() {
			this.request({
				url: "http://157.122.54.189:9088/image/v3/homepage/vertical",
				data: this.params
			})
			.then(result => {
				if (result.res.vertical.length === 0) {
					this.isEnd = true
					uni.showToast({
						title: "已经没有更多数据了",
						icon: 'none'
					})
					return
				}
				
				if (this.recommend.length === 0) {
					this.recommend = result.res.homepage[1].items
					this.months = result.res.homepage[2]
				}
				// 拼接数据才能保持一个完整的hots
				this.hots = [...this.hots, ...result.res.vertical]
			})
		},
		handlePage() {
			if (!this.isEnd) {
				this.params.skip += this.params.limit
				this.getData()
			}
		},
		scrolltoupper(e) {
			 this.old.scrollTop = e.detail.scrollTop
			 // console.log(scrollTop)
			 if (this.old.scrollTop > 350) {
				 this.backTopValue = true
			 } else {
				 this.backTopValue = false
			 }
		},
		// 控制滚动
		backTop() {
		    //防止抖动
			this.scrollTop = this.old.scrollTop
		    this.$nextTick(function() {
		        this.scrollTop = 0
		    });
		},
		goAlbumDetail(item) {
			uni.navigateTo({
				url: `/pages/album-detail/index?id=${item}`
			})
		}
	},
	filters: {
		formatDate(value) {
		    const date = new Date(value)
		    // console.log(date)
		    return format(date, 'dd日/mm月')
		}
	},
	created() {
		this.getData()
	},
	components: {
		getDetail
	}
}
